.product-navbar {
    li {
        width: 462px;
        height: 103px;
        .sunbar {
            width: 462px;
            height: 103px;
        }
        a {           
            &:hover {                
                border-bottom: 16px solid #AA0601;
            }            
        }   
        p {
            color: #000;
            font-size: 5.25rem;
            margin-left: 8px;
        } 
        img {
            height: 48px;           
        }
        border-left: 1px solid rgba(97, 96, 96, 0.7);
        border-left: 1px solid #DCDCDC;
        &:nth-last-child(1) {
            border-right: 1px solid #DCDCDC;
        }
    }
    
    @include breakpoint(pad) {
        li {
            width: 256px;
            height: 69px;
            .sunbar {
                width: 256px;
                height: 69px;
            }
            p {
                font-size: 3.5rem;
            }
            img {
                height: 32px;
            }
        }
    }
    @include breakpoint(phone) {
        li {
            width: 125px;
            height: 35px;
            .sunbar {
                width: 125px;
                height: 35px;
            }
            p {
                font-size: 1.75rem;
                margin-left: 5px;
            }
            img {
                height: 15px;

            }
        }
    }
}
.product-banner {
    img {
        width: 50%;
        &:nth-child(1){
            margin-right: 30px;
        }
    }
    @include breakpoint(pad) {
        img {
            &:nth-child(1){
                margin: 0px;
            }
        }
    }
}
.product-main {
    padding: 88px 0 156px;
    .product-title {
        h4 {
            line-height: 63px;
        }
        p {
            margin-bottom: 40px;
        }
    }   
    .product-list li {
        width: 255px;
        margin-bottom: 100px;
        img {
            width: 100%;
            height: 164px;
        }
        .price p:nth-child(2) {
            color: $secondary;
        }
            
    }
    @include breakpoint(pad) {
        padding: 40px 0 104px;
        .product-title {
            h4 {
                line-height: 43px;
            }
            p {
                margin-bottom: 32px;
            }
        } 
        .product-list li {
            width: 330px;
            margin-bottom: 72px;
        }
        
    } 
    @include breakpoint(phone) {
        padding: 30px 0 56px;
        .product-title {
            h4 {
                line-height: 26px;
            }
            p {
                margin-bottom: 20px;
            }
        } 
        .product-list li {
            width: 345px;
            margin-bottom: 32px;
        }
    }
    
    .box {
        display: flex;
        &-color {
            width: 24px;
            height: 24px;
            background: #5F3E2D;
            border-radius: 4px;
            &:nth-child(2) {
                background: #B75929;
                margin-left: 8px;
            }
        }
        @include breakpoint(phone) {
            &-color {
                width: 15px;
                height: 15px;
            }
        }
    }
}